<template>
	<section>
		<div v-title :data-title="this.$route.name"></div>
	  <!--  -->
		<el-card class="card-primary" v-loading="loading">
			<div slot="header">
				
			</div>
			<ul class="account-group">
				<li class="account-item">
					<i class="el-icon-circle-check"></i>
					<label></label>
					<span></span>
					<el-button size="small" type="info" @click="modifyPass"></el-button>
				</li>
				<li v-if="userInfo.emailVerified === 1" class="account-item">
					<i class="el-icon-circle-check"></i>
					<label></label>
					<span></span>
					<el-button size="small" type="primary" @click="handleVerified"></el-button>
				</li>
				<li v-else class="account-item">
					<i class="el-icon-warning"></i>
					<label></label>
					<span></span>
					<el-button size="small" type="success" @click="verifyEmail"></el-button>
				</li>
				<li v-if="userInfo.mobile" class="account-item">
					<i class="el-icon-circle-check"></i>
					<label></label>
					<span> {{mobile}}</span>
					<el-button size="small" type="info" @click="modifyMobile"></el-button>
				</li>
				<li v-else class="account-item">
					<i class="el-icon-warning"></i>
					<label></label>
					<span></span>
					<el-button size="small" type="success" @click="bindMobile"></el-button>
				</li>
			</ul>
		</el-card>
		<!--  -->
		<el-dialog :visible.sync="passwordVisible" title="">
			<el-row>
				<el-col :span="18" :offset="3">
					<el-form :model="passwordForm" ref="passwordForm" :rules="rules" label-width="120px">
						<el-form-item label="" prop="oldPass">
							<el-input type="password" v-model.trim="passwordForm.oldPass" placeholder=""></el-input>
						</el-form-item>
						<el-form-item label="" prop="newPass">
							<el-input type="password" v-model.trim="passwordForm.newPass" placeholder=""></el-input>
						</el-form-item>
						<el-form-item label="" prop="confirmPass">
							<el-input type="password" v-model.trim="passwordForm.confirmPass" placeholder=""></el-input>
						</el-form-item>
					</el-form>
				</el-col>
			</el-row>
			<div slot="footer">
				<el-button @click="passwordVisible = false"></el-button>
				<el-button type="primary" @click="submitPass"></el-button>
			</div>
		</el-dialog>
		<!--  -->
		<el-dialog :visible.sync="mobileFormVisible" title="">
			<el-row>
				<el-col :span="14" :offset="5">
					<el-form :model="mobileForm" ref="mobileForm" :rules="rules" label-width="120px">
						<el-form-item v-if="!userInfo.mobile" label="" prop="mobile">
							<el-input v-model.trim="mobileForm.mobile" placeholder="" style="width: 90%"></el-input>
						</el-form-item>
						<el-form-item v-if="userInfo.mobile" label="">
							<span>{{mobile}}</span>
						</el-form-item>
						<el-form-item v-if="userInfo.mobile" label="" prop="mobile">
							<el-input v-model.trim="mobileForm.mobile" placeholder="" style="width: 90%"></el-input>
						</el-form-item>
						<el-form-item label="" prop="smsCode">
							<el-row>
								<el-col :span="15">
									<el-input v-model.trim="mobileForm.smsCode" placeholder=""></el-input>
								</el-col>
								<el-col :span="8" :offset="1">
									<el-button type="success" :disabled="disabled" @click="getSmsCode">{{buttonText}}</el-button>
								</el-col>
							</el-row>
						</el-form-item>
					</el-form>
				</el-col>
			</el-row>
			<div slot="footer">
				<el-button @click="mobileFormVisible = false"></el-button>
				<el-button type="primary" @click="submitMobile"></el-button>
			</div>
		</el-dialog>
	</section>
</template>
<script>
	import { getMyinfo, updatePwd, } from '@/api'
	import Md5 from '@/assets/js/md5'
	export default {
		data () {
			const validatePass = (rule, value, callback) => {
        if (!value) {
          return callback(new Error(''));
        }
        if (value !== this.passwordForm.newPass) {
          callback(new Error(''));
        } else {
        	callback()
        }
      }
      const validateMobile = (rule, value, callback) => {
        if (!value) {
          return callback(new Error(''));
        }
        setTimeout(() => {
          if (!value.match(/^(13|14|15|17|18)\d{9}$/)) {
            callback(new Error(''));
          } else {
          	callback()
          }
        }, 0);
      }
			return {
				loading: false,
				userInfo: {},
				passwordVisible: false,
				passwordForm: {
					oldPass: '',
					newPass: '',
					confirmPass: ''
				},
				mobileFormVisible: false,
				mobileForm: {
					mobile: '',
					newMobile: '',
					smsCode: '',
				},
				rules: {
					oldPass: [
						{ required: true, message: '', trigger: 'blur'}
					],
					newPass: [
						{ required: true, message: '', trigger: 'blur'},
						{ min: 8, max: 20, message: ' 8  20 ', trigger: 'blur'},
					],
					confirmPass: [
						{ required: true, validator: validatePass, trigger: 'blur'}
					],
					mobile: [
						{ required: true, validator: validateMobile, trigger: 'blur'}
					],
					smsCode: [
						{ required: true, message: '', trigger: 'blur'}
					],
				},
				disabled: false,
				buttonText: '',
				sendEmail: false,
			}
		},
		methods: {
			// 
			getUserInfo() {
				this.loading = true;
				getMyinfo().then(res => {
					console.log(res)
					this.loading = false;
					if(res.data.code === '0001') {
						this.userInfo = res.data.result.userInfo;
					} else {
						this.$message.error(res.data.message)
					}
				}).catch(err => {
					// console.log(err)
					this.loading = false;
					this.$catchError(err)
				})
			},
			modifyPass() {
				this.passwordVisible = true
			},
			// 
			submitPass () {
				this.$refs.passwordForm.validate(valid => {
					if(valid) {
						let data = {
							oldPassword: Md5.hex_md5(this.passwordForm.oldPass),
							newPassword: Md5.hex_md5(this.passwordForm.newPass),
						}
						updatePwd(data).then(res => {
							if(res.data.code === '0001') {
								this.$message.success(res.data.message)
							} else {
								this.$message.error(res.data.message)
							}
						}).catch(err => {
							console.log(err)
							this.$catchError(err)
						})
						this.passwordVisible = false;
					} else {
						console.log('err submit')
					}
				})
			},
			handleVerified() {
				this.$message.success(`${this.userInfo.email}`)
			},
			// 
			verifyEmail() {
				!this.sendEmail && 
				this.$message(`${this.userInfo.email}`) || 
				this.$notify({ type: 'warning', title: '', message: '' })
				this.sendEmail = true;
			},
			// 
			bindMobile() {
				this.mobileFormVisible = true; 
			},
			countDown() {
				let count = 10;
				let timer = null;
				this.disabled = true;
				timer = setInterval(() => {
					if(count > 0) {
						this.buttonText = ` ${count}s`
						count --;
					} else {
						this.buttonText = '';
						this.disabled = false;
						clearInterval(timer)
					}
				}, 1000)	
			},
			// 
			getSmsCode() {
				this.$refs.mobileForm.validateField('mobile', (errMessage) => {
					if(!errMessage){
						this.countDown()
						this.$message('')
					} else {
						console.log('err submit')
					}
				})
			},
			// 
			submitMobile() {
				this.$refs.mobileForm.validate(valid => {
					console.log(valid)
					if(valid) {
						let data = {
							mobile: this.mobileForm.mobile,
							smsCode: this.mobileForm.smsCode
						}
						console.log(data)
					} else {
						console.log('err submit')
					}
				})
			},
			modifyMobile() {
				this.mobileFormVisible = true;
			}
		},
		computed: {
			mobile() {
				return this.userInfo.mobile && 
							 this.userInfo.mobile.replace(/(\d{3})\d{4}(\d{4})/, '$1****$2')
			}
		},
		mounted () {
			this.getUserInfo()
		}
	}
</script>
<style scoped lang="scss">
	.account-group {
		.account-item {
			display: flex;
			padding: 15px 10px;
			line-height: 36px;
			border-bottom: 1px solid #ddd;
			&:last-child {
				border: 0
			}
			i {
				width: 50px;
				padding-top: 6px;
				font-size: 24px;
				&.el-icon-circle-check {
					color: #7ABD54
				}
				&.el-icon-warning {
					color: #FFCC01;
				}
			}
			label {
				width: 130px;
				font-size: 18px
			}
			> span {
				flex: 1;
				padding: 0 15px;
			}
		}
	}
</style>


