<template>
  <transition name="slide-fade">
    <div class="container">
      <div v-title :data-title="this.$route.name"></div>
      <el-form :model="loginForm" :rules="loginRules" ref="loginForm" class="login-form" :class="{'animated shake': invalid}">
        <h2 class="page-header"></h2>
        <el-form-item label="  " prop="name" label-width="90px">
          <el-input v-model.string="loginForm.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=" " prop="pass" label-width="90px">
          <el-input type="password" v-model="loginForm.pass" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="checkPass" label-width="90px">
          <el-input type="password" v-model="loginForm.checkPass" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="authcode" label-width="90px">
          <el-input type="text" v-model="loginForm.authcode" placeholder="" style="float: left; width: 65%; margin-right: 15px;"></el-input>
          <canvas id="canvasCode" width="80px" height="35px" class="canvas-code" @click="drawCode"></canvas>
        </el-form-item>
        <el-form-item prop="agreement" class="m-b-5">
          <el-checkbox-group v-model="loginForm.agreement">
            <el-checkbox name="agreement" label="" v-model="checked"></el-checkbox>
            <a href="javascript:;" @click="dialogVisible = true"></a>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item class="m-b-5">
          <el-button 
          type="primary" 
          :loading="logging"
          style="width: 100%" 
          @click="submitForm">
             
          </el-button>
        </el-form-item>
        <el-form-item style="margin: 0; text-align: center;">
          <span style="margin: 0 10px">
            
            <router-link to="/login"></router-link>
          </span>
        </el-form-item>
      </el-form>
      <el-dialog title="" v-model="dialogVisible">
        <div class="content" style="height: 350px; overflow-y: auto">
          <h4></h4>
          <p> ""www.yueshijue.com""""</p>
          <h5> 1 </h5>
          <p><strong>1.1</strong> </p>
          <p><strong>1.2</strong></p>
          <p><strong>1.3</strong>18</p>
          <p><strong>1.4</strong></p>
          <h5> 2 </h5>
          <p><strong>2.1</strong></p>
          <p><strong>2.2</strong>12 </p>
          <h5> 3 </h5>
          <p><strong>3.1</strong> </p>
          <p><strong>3.2</strong>/ </p>
          <p><strong>3.3</strong> </p>
          <p><strong>3.4</strong><strong></strong></p>
          <p><strong>3.5</strong><strong></strong></p>
          <p><strong>3.6</strong><strong></strong></p>
          <h5> 4 </h5>
          <p> </p>
          <p><strong>1</strong> </p>
          <p><strong>2</strong></p>
          <p><strong>3</strong> </p>
          <p><strong>4</strong></p>
          <p><strong>5</strong></p>
          <p><strong>6</strong></p>
          <p><strong>7</strong></p>
          <p><strong>8</strong></p>
          <p><strong>9</strong></p>
          <p> </p>
          <p><strong></strong></p>
          <p><strong></strong></p>
          <h5> 5 </h5>
          <p> </p>
          <p> """"</p>
          <h5> 6 </h5>
          <p><strong>6.1</strong><span></span> </p>
          <p><strong>6.2</strong><strong></strong> </p>
          <p> <strong>6.3</strong><strong></strong> </p>
          <h5> 7 </h5>
          <p><strong>7.1</strong> </p>
          <p><strong>7.2</strong></p>
          <p><strong>1</strong> </p>
          <p><strong>2</strong></p>
          <p><strong>3</strong></p>
          <p><strong>4</strong></p>
          <h5> 8 </h5>
          <p><strong>8.1</strong><strong></strong> </p>
          <p><strong>8.2</strong><strong>2011</strong> </p>
          <p> <strong>8.3</strong><strong></strong> </p>
          <p><strong>8.4</strong><strong>,,,</strong> </p>
          <p><strong>8.5</strong><strong>,</strong> </p>
          <p><strong>8.6</strong> </p>
          <h5> 9 </h5>
          <p><strong>,""""</strong> </p>
          <p> <strong>,</strong> </p>
          <p><strong></strong></p>
          <p><strong></strong></p>
          <h5> 10 </h5>  <strong><em></em></strong> 
          <h5> 11 </h5>   
          <h5> 12  </h5>
          <p><strong>12.1</strong></p>
          <p><strong>12.2</strong> </p>
          <p><strong>12.3</strong><span></span></p>
          <p><strong>12.4</strong><span>""</span> </p>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button 
            type="danger" 
            style="width: 100%" 
            @click="dialogVisible = false">
             
          </el-button>
        </span>
      </el-dialog> 
    </div>
  </transition>
</template>
<script>
import utils from '@/assets/js/utils'
import { requestRegist } from '@/api'
export default {
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else {
        if (this.loginForm.checkPass !== '') {
          this.$refs.loginForm.validateField('checkPass');
        }
        callback();
      }
    }
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else if (value !== this.loginForm.pass) {
         callback(new Error(''))
      } else {
        callback()
      }
    }
    var validateCode = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else if (value.toUpperCase() !== this.authCode.toUpperCase()) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    var validateAgreement = (rule, value, callback) => {
      console.log(value)
      if (value) {
        callback()
      } else {
        callback(new Error(''));
      }
    }
    return {
      dialogVisible: false,
      invalid: false,
      logging: false,
      checked: true,
      authCode: '',
      loginForm: {
        name: '',
        pass: '',
        checkPass: '',
        authcode: '',
        agreement: [],
      },
      loginRules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'email', message: '', trigger: 'blur' }
        ],
        pass: [
          { required: true, validator: validatePass, trigger: 'blur' },
          { min: 8, max: 20, message: ' 8  20 ', trigger: 'blur'}
        ],
        checkPass: [
          { required: true, validator: validatePass2, trigger: 'blur' }
        ],
        authcode: [
          { required: true, validator: validateCode, trigger: 'blur'}
        ],
        agreement: [
          { type: 'array', required: true, message: '', trigger: 'change' }
        ]
      }
    };
  },
  methods: {
    submitForm() {
      this.$refs.loginForm.validate((valid) => {
        if (valid && !this.logging) {
          this.logging = true
          requestRegister(this.loginForm).then(res => {
            if (res.data.status === 1) {
              this.$message({
                type: 'success',
                message: res.data.message
              })
              this.$router.push({ path: '/login' })
            } else {
              this.$message({
                type: 'error',
                message: res.data.message
              })
              this.drawCode()
            }
            this.logging = false
          })
        } else {
          this.invalid = true
          console.log('error submit!!');
          return false;
        }
      })
    },
    drawCode () {
      this.authCode = utils.canvasCode('canvasCode')
    }
  },
  mounted () {
    this.drawCode()
  }
}
</script>
<style scoped>
  h4 {
    font-weight: bold;
    margin-bottom: 15px;
  }
  h5 {
    margin: 5px 0;
  }
  p {
    margin: 10px 0;
    line-height: 1.3
  }
  .container {
    overflow: hidden
  }
	.login-form {
		width: 430px;
		margin: 8% auto 0;
		padding: 15px 30px;
		border-radius: 5px;
		background: #fff
	}
  .canvas-code {
    float: right;
    cursor: pointer;
    border-radius: 4px;
    background-image: linear-gradient(-45deg, #111, #666, #222, #777);
  }
  .slide-fade-enter-active {
    transition: all .5s;
  }
  .slide-fade-enter, .slide-fade-leave-active {
    opacity: 0;
    transform: translateY(-30px);
  }
  .animated {
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    z-index: 100
  }

  .animated.infinite {
    -webkit-animation-iteration-count: infinite;
    animation-iteration-count: infinite
  }

  .animated.hinge {
    -webkit-animation-duration: 2s;
    animation-duration: 2s
  }
  @-webkit-keyframes shake {
    0%,
    100% {
      -webkit-transform: translateX(0);
      transform: translateX(0)
    }
    10%,
    30%,
    50%,
    70%,
    90% {
      -webkit-transform: translateX(-10px);
      transform: translateX(-10px)
    }
    20%,
    40%,
    60%,
    80% {
      -webkit-transform: translateX(10px);
      transform: translateX(10px)
    }
  }

  @keyframes shake {
    0%,
    100% {
      -webkit-transform: translateX(0);
      -ms-transform: translateX(0);
      transform: translateX(0)
    }
    10%,
    30%,
    50%,
    70%,
    90% {
      -webkit-transform: translateX(-10px);
      -ms-transform: translateX(-10px);
      transform: translateX(-10px)
    }
    20%,
    40%,
    60%,
    80% {
      -webkit-transform: translateX(10px);
      -ms-transform: translateX(10px);
      transform: translateX(10px)
    }
  }

  .shake {
    -webkit-animation-name: shake;
    animation-name: shake
  }
</style>



